.container{
    margin: 0;
    background-color: white;
}

nav{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(255, 255, 255, 0.6);
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
}

.nav_home{
    position: fixed;
}

.title-div{
    margin-left: 20px;
}

.title-text{
    font-size: 23px;
    font-family: arial;
    font-weight: bold;

    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.6);
}

.sign-buttons{
    display: flex;
    gap: 10px;

    margin-right: 20px;
}

.sign-button{
    font-size: 13px;
    font-family: arial;
    font-weight: bold;

    padding: 5px;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 20px;
    border: solid 1px black;

    cursor: pointer;
    transition: 0.3s;
}

.sign-button:hover{
    transform: scale(1.05);
}

.signin{
    background-color: white;
    transition: 0.3s;
}

.signin:hover{
    background-color: rgb(220, 220, 220);
}

.signup{
    background-color: black;
    color: whitesmoke;
    transition: 0.3s;
}

.signup:hover{
    background-color: rgb(43, 43, 43);
}


.main-hero{
    width: 100%;
    height: 100vh;
}

/*HOME*/
.main-home{
    display: flex;
    flex-direction: column;
}

.main-home section{
    width: 100%;
    height: 100vh;
}

#section1{
    display: flex;
    justify-content: center;
}

.section1-div{
    width: 80%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 50px;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.title-home{
    cursor: pointer;
}

.welcome-text{
    font-size: 8vw;
    font-family: "Tangerine", cursive;
    line-height: 0.8;

    margin: 0;
}

.buysell-buttons{
    display: flex;
    gap: 30px;

}

.buysell-button{
    font-size: 13px;
    font-family: arial;
    font-weight: bold;

    width: 300px;
    height: 50px;
    border-radius: 50px;
    border: solid 1px black;

    cursor: pointer;
    transition: 0.3s;
}

.buysell-button:hover{
    transform: scale(1.05);
}

.buy{
    background-color: white;
    transition: 0.3s;
}

.buy:hover{
    background-color: rgb(220, 220, 220);
}

.sell{
    background-color: black;
    color: whitesmoke;
    transition: 0.3s;
}

.sell:hover{
    background-color: rgb(43, 43, 43);
}

#section2{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.section2-div{
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.section2-div h2{
    font-family: arial;
    font-size: 30px;
}

.artist-card-div{
    display: flex;
    gap: 50px;

    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

}

.artist-card{
    font-family: arial;
    font-size: 15px;

    width: 350px;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.artist-card img{
    width: 100%;
    border-radius: 10px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7);
}

.artist-button{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    padding-bottom: 0;
    padding-top: 0;
}

.contact-button{
    font-size: 10px;
    font-family: arial;
    font-weight: bold;

    padding: 5px;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 20px;
    border: solid 1px black;
    background-color: white;
    color: black;

    cursor: pointer;
    transition: 0.3s;
}

.contact-button:hover{
    transform: scale(1.05);
    background-color: rgb(221, 221, 221);
}

#section3{
    height: 92vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.sell-form{
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;

    background-color: rgb(255, 218, 253);
    padding: 50px;
    border-radius: 15px;
}

.showcase-text{
    font-size: 40px;
    font-family: "Tangerine", cursive;
    font-weight: bold;
    margin: 0;
    margin-bottom: 10px;
}

.sell-form label{
    font-size: 15px;
    font-family: arial;

    font-weight: bold;
}

.inputs-div{
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
}

.inputs-div input{
    width: 90%;
    height: 30px;

    border-radius: 50px;
    padding-left: 5%;
    padding-right: 5%;

    background-color: rgba(255, 255, 255, 0.5);
}

.input-file{
    margin-top: 10px;
}

.upload-button{
    width: 100%;
    height: 30px;
    border-radius: 50px;
    margin-top: 10px;

    font-size: 13px;
    font-family: arial;
    font-weight: bold;
    background-color: black;
    color: white;
}

.inputs-div br{
    line-height: 0.5;
}

footer{
    width: 100%;
    height: 50px;
    background-color: white;
    box-shadow: -1px -1px 5px rgba(0, 0, 0, 0.5);

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.copyright-div{
    font-size: 13px;
    font-family: arial;

    margin-left: 20px;
}

.contact-div{
    display: flex;
    gap: 20px;

    margin-right: 20px;
}

.contact-div img{
    height: 30px;
    width: 30px;
    opacity: 70%;
    cursor: pointer;

    transition: 0.3s;
}

.contact-div img:hover{
    opacity: 100%;
    transform: scale(1.05);
}